.container {
  background: #eff3f8;
}

.menuItems {
  display: flex;
  width: 90%;
  margin: 0 auto;
  justify-content: space-between;
  flex-wrap: wrap;
}

.menuItem {
  width: calc(100% / 2 - 40px);
  height: 300px;
  border: 3px solid #726666;
  margin-bottom: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  text-align: center ;
  transition: transform .3s ease-in-out;
  p {
    font-size: 30px;
    font-weight: bold;
  }
  &:hover {
    transform: scale(1.1);
  }
}

