.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 50px;
  background: #fff;
  margin-bottom: 40px;
  box-shadow: 0 0 20px 0 #00000038;
  h1 {
    margin: 0;
  }
}

.headerRight {
  display: flex;
  align-items: center;

  p {
    margin: 0;
  }
}


.userInfo {
  margin-right: 50px;
  font-weight: bold;
  font-size: 18px;
}
