.container {
  background: #eff3f8;
  height: 100vh;
}

.menuItems {
  width: 90%;
  margin: 0 auto;
}

.menuItem {
  height: 300px;
  border: 3px solid #726666;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  text-align: center ;
  transition: transform .3s ease-in-out;
  p {
    font-size: 30px;
    font-weight: bold;
  }
  &:hover {
    transform: scale(1.1);
  }
}

